<template>
  <div class="cms-list-layout">
     <cms-prompt>
          <li>服务价格调整只针对后续购买的服务有效，服务购买支付后方能使用该服务工具，如果服务收费为0元时则不收费</li>
        </cms-prompt>
    <el-table :data="tableData" style="width: 100%;margin-top:20px;" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column width="60"></el-table-column>
      <el-table-column prop="promoteType" label="服务收费项目" min-width="120px">
        <template slot-scope="scope">
          <span v-if="scope.row.promoteType==1">单品折扣</span>
           <span v-if="scope.row.promoteType==2">单品一口价</span>
            <span v-if="scope.row.promoteType==3">满减</span>
             <span v-if="scope.row.promoteType==4">优惠券</span>    
              <span v-if="scope.row.promoteType==5">满赠</span>
               <span v-if="scope.row.promoteType==6">包邮</span>
          </template>   
      </el-table-column>
      <el-table-column prop="price" label="有效期30天">
            <template slot-scope="scope">
              {{$cms.float(scope.row.price)}} 元
            </template>
      </el-table-column>
      <el-table-column prop="hasShopNum" label="状态">
          <template slot-scope="scope">
        <span v-if="scope.row.hasOpenService" class="success">已开通</span>
        <span v-if="!scope.row.hasOpenService&&scope.row.price>0" class="link" 
          @click="$routerLink('/marketingService/pay','pay',{data:scope.row})">去开通</span>
        <span v-if="!scope.row.hasOpenService&&scope.row.price<=0" class="link"
         @click="$routerLink('/marketingService/pay','pay',{data:scope.row})"
        >开通</span>
          </template>
      </el-table-column>
       <el-table-column prop="price" label="到期时间">
            <template slot-scope="scope">
              {{$moment(scope.row.serviceEndTime,'YYYY-MM-DD')}}
            </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import {
  promoteConfigList,
  promoteConfigUpdate
} from './../../../api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  id: '',
  price: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    save(state) {
      const datas = []// 构造修改的数据
      this.tableData.forEach(v => {
        const obj = {
          id: v.id,
          price: v.price === '' ? 0 : v.price
        }
        datas.push(obj)
      })
      promoteConfigUpdate({ datas: datas }).then(res => {
        if (res.code === 200) {
          this.$successMessage('操作成功')
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData(promoteConfigList, {}, false)
  }
}
</script>




